//Слой отображения
import { ensureElement } from '../utils/utils';
import { Component } from './base/component';
// Отрисовывает карточку, работает с разметкой
export class ProductCard extends Component {
    constructor(container, actions) {
        super(container);
        this.Category = {
            'софт-скил': 'card__category_soft',
            'хард-скил': 'card__category_hard',
            'дополнительное': 'card__category_additional',
            'другое': 'card__category_other',
            'кнопка': 'card__category_button',
        };
        this._title = ensureElement('.card__title', container);
        this._image = container.querySelector('.card__image');
        this._button = container.querySelector('.card__button');
        this._description = container.querySelector('.card__text');
        this._price = container.querySelector('.card__price');
        this._category = container.querySelector('.card__category');
        this._cartItemIndex = container.querySelector('.basket__item-index');
        if (actions === null || actions === void 0 ? void 0 : actions.onClick) {
            if (this._button) {
                this._button.addEventListener('click', actions.onClick);
            }
            else {
                container.addEventListener('click', actions.onClick);
            }
        }
    }
    set id(value) {
        this._id = value;
    }
    get id() {
        return this._id || '';
    }
    set title(value) {
        this.setText(this._title, value);
    }
    set image(value) {
        this.setImage(this._image, value, this.title);
    }
    set description(value) {
        this.setText(this._description, value);
    }
    set category(value) {
        this.setText(this._category, value);
        this.toggleClass(this._category, this.Category[value], true);
    }
    set price(value) {
        this.setText(this._price, value ? `${value.toString()} синапсов` : 'Бесценно');
        if (value === null && this._button) {
            this._button.disabled = true;
        }
    }
    set button(value) {
        this.setText(this._button, value);
    }
    set inCart(value) {
        this.changeButtonDescription(value);
    }
    set cartItemIndex(value) {
        this._cartItemIndex.textContent = value;
    }
    changeButtonDescription(inCart) {
        if (inCart) {
            this.button = 'Удалить из корзины';
        }
        else {
            this.button = 'В корзину';
        }
    }
}
