import { createElement, ensureElement } from "../utils/utils";
import { Component } from "./base/component";
// Отрисовывает корзину с ее содержимым
export class ShoppingCart extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._list = ensureElement('.basket__list', this.container);
        this._total = this.container.querySelector('.basket__price');
        this._button = this.container.querySelector('.button');
        if (this._button) {
            this._button.addEventListener('click', () => {
                events.emit('goToOrder:submit');
            });
        }
    }
    set items(items) {
        if (items.length) {
            this._list.replaceChildren(...items);
        }
        else {
            this._list.replaceChildren(createElement('p', {
                textContent: 'Корзина пуста'
            }));
        }
    }
    set buttonToggler(items) {
        if (!items.length) {
            this.setDisabled(this._button, true);
        }
        else {
            this.setDisabled(this._button, false);
        }
    }
    set total(summ) {
        this.setText(this._total, `${summ.toString()} синапсов`);
    }
    resetCartView() {
        this.items = [];
    }
}
