import { Component } from "./base/component";
import { ensureElement } from "../utils/utils";
// Отвечает за наполнение страницы, устанавливает элементы в предназначенные для них контейнеры,
// работает со счетчиком товаров в корзине (выводит обновленное значение)
export class Page extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._counter = ensureElement('.header__basket-counter');
        this._catalog = ensureElement('.gallery');
        this._wrapper = ensureElement('.page__wrapper');
        this._shoppingCartIcon = ensureElement('.header__basket');
        this._shoppingCartIcon.addEventListener('click', () => {
            this.events.emit('shoppingCart:select');
        });
    }
    set counter(value) {
        this.setText(this._counter, String(value));
    }
    set catalog(items) {
        this._catalog.replaceChildren(...items);
    }
    set locked(value) {
        if (value) {
            this.toggleClass(this._wrapper, 'page__wrapper_locked', true);
        }
        else {
            this.toggleClass(this._wrapper, 'page__wrapper_locked');
        }
    }
}
